// 创建云盘
import service from "../http"
import { getEcsOptions }from '../common'

export default{
    //创建云盘
    create(req){
        return service(getEcsOptions('POST','/ebs/create_disk/',req))
    },
    //获取云盘类型信息
    get_disk_type(req){
        return service(getEcsOptions('GET','/ebs/disk_info/',req))
    },
    //获取云盘价格
    get_price(req) {
        return service(getEcsOptions('POST', '/ecs/get_price/', req))
    },
    //获取云盘扩容价格
    get_capacity_price(req) {
        return service(getEcsOptions('POST', '/ebs/get_ebs_price/', req))
    },
    //获取云盘剩余可使用额度
    get_disk_limit(req) {
        return service(getEcsOptions('POST', '/ebs/get_disk_limit/', req))
    },
    //获取云盘详情
    get_disk_detail(req) {
        return service(getEcsOptions('GET', '/ebs/get_disk_detail/', req))
    },
    //获取云盘详情
    getDiskRules(req) {
        return service(getEcsOptions('POST', '/ebs/disk_rules/', req))
    },
}